body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn {
  display: block;
  padding: 15px 120px;
  margin-top: 20px;
}
#inputbox {
  display: inline-block;
  vertical-align: top;
  height: 100px;
  width: 500px;
  padding: 20px;
}

#username {
  height: 30px;
  padding: 10px 10px 10px 0px;
  font-size: 20px;
}

#userlabel {
  padding: 10px 10px 10px 0px;
}

#userinputbox {
  height: 30px;
  width: 200px;
  border: 5px solid grey;
  
  
}
#gitscore {
  display: inline-block;
  width: 300px;
  height: 200px;
  vertical-align: top;
}
#gitboard {
  height: 500px;
  width: 900px;
  background-color: peachpuff;
  padding: 10px;
  margin: 100px;
}

#header {
  margin-left: 10px;
}
